<template>
  <div class="realauthResult">
    <div class="head-title" v-if="type=='success'">
        <div class="title" >
          <img src="../../assets/image/authenSuccess.png"/>
        </div>
        <div class="subTitle">
           欢迎 {{name}}
        </div>
    </div>
      <div class="head-title" v-else-if="type=='wait'">
          <div class="title" >
              <img src="../../assets/image/wait_info.png"/>
          </div>
          <div class="subTitle">
              你好  {{name}}，我们已收到您的实名认证申请，预计1～3个工作日，请耐心等候审核。

          </div>
      </div>
      <div class="head-title" v-else>
          <div class="title" >
              <img src="../../assets/image/authenFailed.png"/>
          </div>
          <div class="subTitle" v-if="type=='error'">
              抱歉  {{name}}，因未知错误暂时未能完成实名认证，请与燃象联系。
          </div>


          <div class="subTitle" v-else>
              抱歉  {{name}}，您填写的实名信息存在错误，请返回并重新填写。
          </div>
      </div>

    <div  class="buttonGroup">
      <div  class="button">
        <van-button  block @click="goToHome"  v-if="type=='success'">
          继续交易
        </van-button>
          <van-button  block  v-else-if="type=='error'">
              拨打 4001234678
          </van-button>
          <div v-else-if="type=='wait'">
          </div>
          <van-button  block  v-else @click="goToRealAuthor">
              重新填写
          </van-button>
      </div>
    </div>
      <div class="back"  v-if="type=='wait'||type=='error'">
          <van-icon name="cross" @click="goToHome"/>
      </div>
  </div>
</template>

<script>
import { reactive, toRefs,onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'

export default {
  setup() {
    const router=useRouter();
      const route = useRoute()

      const state = reactive({
           name:'',
          type:''
    })

      onMounted(async () => {
          const {type,name} = route.params;
          state.name=name;
          state.type=type;

          setTimeout(()=>{
              state.type='';
              setTimeout(()=>{
                  state.type='error';
                  setTimeout(()=>{
                      state.type='success';
                  },5000)
              },5000)
          },5000)
      })
    const goToHome = () => {
      router.push({ path: `/home` })
    }
    const goToRealAuthor= () => {
        router.push({ path: `/real-author` })
    }

       return {
      ...toRefs(state),
         goToHome,
           goToRealAuthor

       }
  }

}
</script>

