<script>
import { reactive, onMounted, toRefs, nextTick } from 'vue'
import { useRouter } from 'vue-router'
import sHeader from '@/components/SimpleHeader'
export default {
  setup() {
    const router = useRouter()

    const state = reactive({
      show:false,
      remindTime:0,
        onsalecheck:false,
      timeSels:['5分钟','10分钟','15分钟','20分钟','25分钟','30分钟'],
        artist: {
      }
    })

    onMounted(async () => {
     // const { id } = route.params
      //const { data } = await getDetail(id)
    /*  Toast.loading({
        message: '加载中...',
        forbidClick: true
      });*/
      let dataInfo={

              author:'Mee Cats',
              company:'',
              id: 5,
              banner: require("../assets/image/artist_pic_1.png"),
              avatar: require("../assets/image/artist_pic_3.png"),
              introduce:'M Studio 成立于2021年，是 NFT 领域的新晋选手，由文化艺术领域和互联网技术领域的专业年轻团队创立，目前聚焦在视频艺术领域。',
                products:[{
                    status:0,
                    isSeries:false,
                    pic: require("../assets/image/artist_pic_4.png"),
                    title: "神秘t3任务"
                }, {
                    status:1,
                    isSeries:false,
                    pic: require("../assets/image/artist_pic_2.png"),
                    title: "最后一卷胶卷"
                }, {
                    status:2,
                    isSeries:true,
                    pic: require("../assets/image/artist_pic_5.png"),
                        title: "镜头重现"
                }, {
                    status:2,
                    totalNum:0,
                    author:'南京江北新区数字经济产业园',
                    pic: require("../assets/image/artist_pic_6.png"),
                    title: "迷雾"
                }]
          }
      const {data} = {
        "resultCode": 200,
        "message": "SUCCESS",
        "data": dataInfo
      };

      state.artist = data
     // Toast.clear()
    })

    nextTick(() => {
      const content = document.querySelector('.detail-content')
      content.scrollTop = 0
    })

    const goBack = () => {
      router.go(-1)
    }

    const goToArtistProducts = (item) => {
          router.push({ path: `/artist-product/${item.id}` })
    }
    
    const selRemindTime=(index)=>{
      state.remindTime=index;
    }
    const showRemindersDialog=()=>{
      state.show=true;
    }
    const confirmRemindDialog=()=>{
      state.show=false;
    }
    return {
      ...toRefs(state),
      goBack,
      showRemindersDialog,
      confirmRemindDialog,
      selRemindTime,
        goToArtistProducts
    }
  },
  components: {
    sHeader
  }
}
</script>
