                                    class="van-image__img" style="object-fit: contain;"/>

                            <div class="rightText">
                                <span class="text_2">R&nbsp;A&nbsp;N&nbsp;X&nbsp;I&nbsp;A&nbsp;N&nbsp;G&nbsp;·&nbsp;燃&nbsp;象</span>
