                            clear-trigger="always"
                            label=""
                            @update:model-value	="onInput"
                            placeholder="输入兑换码"
                    />
                    <p>兑换码最终解释权归燃象所有</p>
                    <div class="button" v-if="exchangeStatus=='1'||exchangeStatus=='0'" :class="{'active':exchangeStatus=='1'}">
                        <svg-icon @click="exchangeCodeHandle"  className="arrow-right" icon-class="arrow-right"></svg-icon>
                        <p></p>
                    </div>

                    <div class="button active" v-else>
                        <!--<svg-icon className="svgicon" :icon-class="exchangeStatus=='2'?'right':'error'"></svg-icon>-->
                        <svg-icon className="svgicon" icon-class="arrow-right"></svg-icon>
                        <p>{{exchangeStatusInfo[exchangeStatus]}}</p>
                    </div>

                </div>
