<script>
    import { reactive, onMounted, toRefs, nextTick,getCurrentInstance } from 'vue'
    import { useRouter } from 'vue-router'
    import { Toast } from 'vant'

    import sHeader from '@/components/SimpleHeader'
    import Result from './Result'
    export default {
        setup() {
            const router = useRouter()
            const {appContext}=getCurrentInstance();

            const state = reactive({
                payResult:false,
                show:false,
                showOverlay:false,
                exchangeCode:'',
                remindTime:0,
                exchangeStatus:'0',
                exchangeStatusInfo:{'2':'兑换成功','4':'兑换码已使用','3':'兑换码错误'},
                handletype:'',
                info:{},
                onsalecheck:false,
                timeSels:['5分钟','10分钟','15分钟','20分钟','25分钟','30分钟'],
                product: {
                },
                modal_show:false,
                active:0,
                payWayTabs:[
                    {
                        title:'银行卡',
                        img: require("../../assets/image/btn_cardPay.png"),
                    },
                    {
                        title:'微信支付',
                        img: require("../../assets/image/btn_weixinPay.png"),
                    },
                    {
                        title:'支付宝',
                        img: require("../../assets/image/btn_zfbPay.png"),
                    }]
            })

            onMounted(async () => {
                // const { id } = route.params
                //const { data } = await getDetail(id)
                /*  Toast.loading({
                    message: '加载中...',
                    forbidClick: true
                  });*/
                let dataInfo={

                    author:'Mee Cats',
                    company:'燃象',
                    id: 5,
                    title:'Astronaut',
                    pic: require("../../assets/image/pic_4.jpg"),
                    price:'69.9'

                }
                const {data} = {
                    "resultCode": 200,
                    "message": "SUCCESS",
                    "data": dataInfo
                };

                state.product = data
                // Toast.clear()
            })

            nextTick(() => {
                const content = document.querySelector('.detail-content')
                content.scrollTop = 0
            })

            const goBack = () => {
                router.go(-1)
            }
            const onInput = () => {
                if(state.exchangeCode=='') state.exchangeStatus='0';
                else state.exchangeStatus='1';
            }
            const exchangeCodeHandle=()=>{
                if(state.exchangeCode=='111') state.exchangeStatus='2';
                else if(state.exchangeCode=='11') state.exchangeStatus='4';
                else state.exchangeStatus='3';
            }
            const goToArtistProducts = (item) => {
                router.push({ path: `/payPage/${item.id}` })
            }

            const selRemindTime=(index)=>{
                state.remindTime=index;
            }
            const showRemindersDialog=()=>{
                state.show=true;true
            }
            const confirmRemindDialog=()=>{
                state.show=false;
            }

            const payHandle=()=>{
                if(state.showOverlay==true){
                    Toast('正在处理请稍等')
                    return;
                }
                state.showOverlay=true;
                setTimeout(()=>{
                    state.showOverlay=false;
                    state.payResult=true;
                    state.handletype='success';
                    state.info={
                        price:'69.9',
                        orderNo:'NFT250944693540',
                        payType:state.payWayTabs[state.active].title,
                        time:appContext.config.globalProperties.$moment(new Date()).format('YYYY-MM-DD hh:mm:SS')
                    }
                },5000)
            }
            const backToPay=()=>{
                state.payResult=false;
            }
            const closeResult = () => {
                 router.push({ path: `/home` })
            }
            const show_exchangeCode_model = () => {
                if(state.showOverlay==true)return;
                state.modal_show=true;

            }

            return {
                ...toRefs(state),
                goBack,
                closeResult,
                showRemindersDialog,
                confirmRemindDialog,
                selRemindTime,
                goToArtistProducts,
                payHandle,
                backToPay,
                onInput,
                exchangeCodeHandle,
                show_exchangeCode_model
            }
        },
        components: {
            sHeader,
            Result
        }
    }
</script>
